import { Card, Image, message } from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import StarRating from "./starRating";

const FALLBACK_IMAGE =
  "data:image/png;base64,";

const DetailCard = ({ params }) => {
  const { id } = useParams();
  const [detail, setDetail] = useState({});
  const [loading, setLoading] = useState(false);

  const apiCallForDetails = async (params) => {
    try {
      if (params) {
        setLoading(true);
        const response = await axios.get(
          `${process.env.React_APP_BACKEND_URL}/search/shows?${params}`
        );

        setDetail(response.data);
        setLoading(false);
      } else {
        setLoading(true);
        const response = await axios.get(
          `${process.env.React_APP_BACKEND_URL}/shows/${id}`
        );
        setDetail(response.data);
        setLoading(false);
      }
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  useEffect(() => {
    apiCallForDetails();
  }, []);

  useEffect(() => {
    if (params) {
      apiCallForDetails(params);
    }
  }, [params]);

  return (
    <Card
      loading={loading}
      cover={
        <div>
          <Image
            alt={"pic"}
            src={detail?.image?.original || FALLBACK_IMAGE}
            preview={false}
          />
        </div>
      }
      className="card"
    >
      <div className="heading">
        <strong>
          {detail?.name || "-"}{" "}
          <span className="rating">{`(${
            detail?.rating?.average || 0
          }/10)`}</span>
        </strong>
        <div className="starComponent">
          <StarRating rating={detail?.rating?.average || 0} />
        </div>
      </div>
      <strong className="subHeading">
        {new Date(detail?.premiered).getFullYear() || "-"} |{" "}
        {detail?.runtime || "-"} | {detail?.name || "-"}
      </strong>
      <p className="summary">{detail?.summary?.replaceAll("<p>", "") || ""}</p>
    </Card>
  );
};

export default DetailCard;
